@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap);
:root{
    --orange: hsl(26, 100%, 55%);
    --pale-orange: hsl(25, 100%, 94%);
    --very-dark-blue:hsl(220, 13%, 13%);
    --dark-grayish-blue: hsl(219, 9%, 45%);
    --grayis-blue: hsl(220, 14%, 75%);
    --light-grayis-blue:hsl(223, 64%, 98%);
    --white: hsl(0, 0%, 100%);
    --black: hsl(0, 0%, 0%);
}
body{
    margin: 0 8vw;
    font-size: 16px;
    color: var(--black);
    font-family: 'Kumbh Sans', sans-serif;
}

@media (max-width: 700px){
    body{
        margin: 0;
    }
}
nav{
    padding: 25px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--grayis-blue);
    position: relative;
}
.menu{
    display: none;
}
.left-nav, .right-nav{
    display: flex;
    align-items: center;
}

.nav-categories{
    display: flex;
    justify-content: left;
    align-items: center;
}

.nav-categories li{
    list-style: none;
    margin: 0 10px;
}
.nav-categories li a{
    padding-bottom: 39px;
    text-decoration: none;
    color: var(--grayis-blue);
    font-weight: bold;
}
.nav-categories li a:hover{
    color: var(--dark-grayish-blue);
    border-bottom: 3px solid var(--orange);
}

.cart{
    position: relative;
    margin-right: 50px;
}
.cart div{
    font-size: 0.7em;
    font-weight: bold;
    color: var(--white);
    height: 16px;
    padding: 0 5px;
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    position: absolute;
    top: -8px;
    right: -8px;
    background: var(--orange);
}

.cart:hover{
    cursor: pointer;
}

.avatar{
    width: 50px;
    height: 50px;
    border: 2px solid var(--white);
}
.avatar:hover{
    cursor: pointer;
    border-radius: 100%;
    border-color: var(--orange);
}

@media (max-width: 700px){
    nav{
        border-bottom: none;
        padding: 25px 1em;
    }
    .menu{
        display: block;
        margin-right: 16px;
    }
    .nav-categories{
        display: none;
    }
    .right-nav{
        justify-content: end;
        width: 50%;
    }
    .cart{
        margin-right: 16px;
    }
    .avatar{
        width: 24px;
        height: 24px;
    }
}
.mobile-menu-nav{
    height: 100vh;
    width: 54vw;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background: var(--white);

    position: absolute;
    top: 0;
    left: 0;

    padding: 24px;
}
.mobile-menu-nav ul{
    padding:0;
    margin: 0;
    margin-top: 32px;
    list-style: none;
}
.mobile-menu-nav ul li{
    margin-bottom: 16px;
}
.mobile-menu-nav ul li a{
    text-decoration: none;
    color: var(--black);
    font-weight: bold;
}
.cart-menu{
    min-height: 20vh;
    width: 20vw;
    background: white;
    border-radius: 5px;

    position: absolute;
    top: 8vh;
    right: 0;
    z-index: 100; 
    box-shadow: 0px 9px 11px -1px rgba(0,0,0,0.22);
}

.cart-menu  h1{
    font-size: 1em;
    padding: 16px 16px;
    border-bottom: 1px solid var(--grayis-blue);
}

.cart-items ul{
    list-style: none;
}
.checkout-cart-button{
    margin: 16px 0;
    border: none;
    padding: 16px 0;
    width: 95%;
    border-radius: 10px;
    background: var(--orange);
    color: var(--white);
    font-weight: bold;
    font-size: 1em;
}

.cart-menu .empty-cart{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    font-weight: 700;
    color: var(--very-dark-blue);
    
}

.cart-menu .cart-items-list{
    padding-left: 16px;
    height: 100%;
    
}



.none{
    display: none;
}

@media (max-width: 700px){
    .cart-menu{
        width: 94vw;
        min-height: 30vh;
        height: auto;
        top: 12vh;
        left: 0;
        right: 0;
        margin: auto;
    }
    .cart-menu .cart-items-list{
        padding-left: 16px;
    }
    
}
.item{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-right: 16px;
    margin: 16px 0;
}

.item-image{
    width: 50px;
    height: auto;
    border-radius: 5px;
    margin-right: 16px;
}

.item-data{
    color: var(--dark-grayish-blue);
    margin-right: 45px;
}
.item-data h1{
    font-weight: 400;
    margin: 0;
    padding: 0;
    margin-bottom: 5px;
    border-bottom: none;
}
.item-data p{
    margin: 0;
    padding: 0;
}
.item-data span{
    font-weight: bold;
    color: var(--black);
}
.trash{
    position: absolute;
    right: 20px;
    cursor: pointer;
}

@media (max-width: 700px){
    .item{
        padding-right: 24px;
    }
    .item-data{
        color: var(--dark-grayish-blue);
        margin-right: 25px;
    }
}
.product-info{
    display: flex;
    margin-top: 10vh;
    justify-content: center;
    align-items: center;
    height: 70vh;
}
.product-info-images{
    height: 70vh;
}
.product-info-images-main{
    height: 50vh;
    border-radius: 10px;
}
.product-info-images-main-group{
    position: relative;
}
.product-info-images-buttons{
    display: none;
}
.product-info-images-buttons button{
    background: var(--white);
    border: none;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.product-info-images-buttons button img{
    background: var(--white);
    border-radius: 100%;
    height: 12px;
    width: 12px;
    padding: 16px;
}
.prev-button{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 16px;
    margin: auto;
}
.next-button{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 16px;
    margin: auto;
}
.product-info-images-sub{
    width: 100%;
    height: 15vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product-info-images-sub div{
    height: 8vh;
    width: auto;
    border-radius: 10px;
    cursor: pointer;
    border: 2px solid var(--white);
}
.product-info-images-sub img:hover{
    opacity: 50%;
}
.product-info-images-sub .active{
    border-color: var(--orange);
}
.active img{
    opacity: 50%;
}
.product-info-images-sub img{
    height: 100%;
    width: auto;
    border-radius: 8px;
}

.product-info-text{
    width: 24vw;
    margin-left: 6vw;
}

.product-info-text-company{
    color: var(--orange);
    text-transform: uppercase;
    font-size: 1em;
}
.product-info-text-title{
    margin-bottom: 24px;
}
.product-info-text-description{
    margin-bottom: 16px;
}
.product-info-text-price{
    position: relative;
}
.product-info-text-price-discount{
    font-size: 0.5em;
    padding: 5px 8px;
    background: var(--pale-orange);
    color: var(--orange);
    border-radius: 10px;
    margin-left: 20px;
}
.product-info-text-price-last-price{
    position: absolute;
    bottom: -1.8em;
    left: 0;
    font-size: 0.5em;
    color: var(--grayis-blue);
    text-decoration: line-through;
}

.product-info-text-buttons{
    margin-top: 10vh;
    display: flex;
    justify-content: left;
    align-items: center;
}
.add-to-cart-button{
    color: var(--white);
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--orange);
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}
.add-to-cart-button span{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 12vw;
}
.add-to-cart-button:hover{
}
.add-to-cart-button img{
    margin-right: 1em;
}

.add-to-cart-units{
    border-radius: 10px;
    background: var(--light-grayis-blue);
    width: 8vw;
    font-weight: bold;
    font-size: 1em;
}
.add-to-cart-units button{
    background: none;
    border: none;
    color: var(--orange);
}
.add-to-cart-units{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-right: 2vw;
}
.add-to-cart-units button{
    cursor: pointer;
}

@media (max-width: 700px){
    .product-info{
        flex-direction: column;
        height: auto;
        margin-top: 0;
        padding-bottom: 2em;
    }
    .product-info-images{
        width: 100%;
        height: auto;
    }
    .product-info-images-main{
        height: auto;
        width: 100%;
        border-radius: 0;
    }
    .product-info-images-main img{
        height: auto;
        width: 100vw;
    }
    .product-info-images-buttons{
        display: block;
    }
    .product-info-images-sub{
        display: none;
    }
    .product-info-text{
        padding: 1em;
        width: auto;
        margin-left: 0;
    }
    
    .product-info-text-price{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .product-info-text-price-discount{
        position: relative;
        margin: 0;
        right: 32px;
    }
    .product-info-text-price-last-price{
        position: relative;
        top: 0;
        left: 0;
        margin-left: 20px;
        font-size: 0.5em;
        color: var(--grayis-blue);
        text-decoration: line-through;
    }
    .product-info-text-buttons{
        display: block;
        margin-top: 1em;
    }
    .add-to-cart-units{
        width: 100%;
        margin-bottom: 1em;
    }
    .add-to-cart-button,
    .add-to-cart-button span{
        width: 100%;
    }
}
